.lessons-section {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 55px;
    justify-content: center;
    padding: 16px;
}

.lesson-card {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    padding: 16px;
    text-align: center;
}

.lesson-image {
    width: 96%;
    height: auto;
    border-radius: 8px;
    object-fit: cover;
}

.lesson-title {
    font-size: 18px;
    font-weight: bold;
    margin-top: 8px;
}

.lesson-description {
    font-size: 14px;
    margin-top: 8px;
    color: #666;
}

.lesson-button {
    margin-top: 16px;
    padding: 10px 20px;
    background-color: #5E8CEA;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.lesson-button:hover {
    background-color: #487EEA;
}

/* Responsive design for tablets */
@media screen and (max-width: 1024px) {
    .lessons-section {
        grid-template-columns: repeat(2, 1fr);
    }
}

/* Responsive design for mobile */
@media screen and (max-width: 768px) {
    .lessons-section {
        grid-template-columns: 1fr;
        gap: 20px;
    }
}
