/* Container for the whole page */
.page-container2 {
  display: flex;
  height: 100vh;
  background-color: #edf3ff;
  position: relative; /* Set container to relative for sidebar positioning */
  flex-direction: column; /* For smaller screens */
}

/* Styling for the left sidebar */
.left-sidebar2 {
  left: 0; /* Position the left sidebar on the left side */
  transform: perspective(600px) rotateY(20deg);
  animation: fireEffect 2s infinite;
  width: 15vw; /* Use vw for responsiveness */
  max-width: 200px; /* Limit the sidebar width */
  display: flex;
  align-items: center;
  justify-content: center;
  background: #edf3ff;
  color: white;
  font-size: 20px;
  font-family: 'Arial', sans-serif;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
  position: absolute; /* Use absolute positioning for both sidebars */
  top: 0;
  bottom: 0;
}

/* Styling for the right sidebar */
.right-sidebar2 {
  right: -100vw; /* Initially offscreen */
  transform: perspective(600px) rotateY(-20deg);
  animation: fireEffect 2s infinite;
  width: 15vw; /* Use vw for responsiveness */
  max-width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #edf3ff;
  color: white;
  font-size: 20px;
  font-family: 'Arial', sans-serif;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
  position: absolute; /* Use absolute positioning for both sidebars */
  top: 0;
  bottom: 0;
  transition: right 0.5s ease; /* Add transition for sliding effect */
}

/* Main content styling */
.content2 {
  flex: 1;
  margin-left: 15vw; /* Space for the left sidebar */
  margin-right: 15vw; /* Space for the right sidebar */
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  transition: margin 0.5s ease; /* Smooth transition for responsive behavior */
}

/* Fire effect for the sidebars */
@keyframes fireEffect {
  0% {
    box-shadow: 0 0 10px rgba(0, 0, 255, 0.3), 0 0 20px rgba(0, 0, 255, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(0, 0, 255, 0.6), 0 0 40px rgba(0, 0, 255, 1);
  }
  100% {
    box-shadow: 0 0 10px rgba(0, 0, 255, 0.3), 0 0 20px rgba(0, 0, 255, 0.5);
  }
}

/* Hover effect for sidebars */
.sidebar:hover {
  animation: blueFireHover 1s infinite;
}

/* Animation when hovering over the sidebars */
@keyframes blueFireHover {
  0% {
    box-shadow: 0 0 15px rgba(0, 0, 255, 0.8), 0 0 30px rgba(0, 0, 255, 1);
  }
  100% {
    box-shadow: 0 0 25px rgba(0, 0, 255, 1), 0 0 50px rgba(0, 0, 255, 1);
  }
}

/* Media query for tablets (768px and below) */
@media (max-width: 768px) {
  .left-sidebar2, .right-sidebar2 {
    width: 25vw; /* Adjust sidebar width for smaller screens */
  }

  .content2 {
    margin-left: 25vw;
    margin-right: 25vw;
  }
}

/* Media query for mobile devices (480px and below) */
@media (max-width: 480px) {
  .left-sidebar2, .right-sidebar2 {
    width: 35vw; /* Increase sidebar width on very small screens */
  }

  .content2 {
    margin-left: 35vw;
    margin-right: 35vw;
  }

  .left-sidebar2, .right-sidebar2 {
    font-size: 16px; /* Adjust font size for mobile */
  }
}
