.page-container {
    display: flex;
    height: 100vh;
    background-color: #edf3ff;
    position: relative; /* Set container to relative for sidebar positioning */
  }
  
  .sidebar {
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #edf3ff;
    color: white;
    font-size: 20px;
    font-family: 'Arial', sans-serif;
    box-shadow: 0 0 15px rgb(237, 243, 255);
    position: absolute; /* Use absolute positioning for left and right */
    top: 0;
    bottom: 0;
  }
  
  .left-sidebar {
    left: 0; /* Position on the left side of the page */
    transform: perspective(600px) rotateY(20deg);
    animation: fireEffect 2s infinite;
  }
  
  .right-sidebar {
    right: 0; /* Position on the right side of the page */
    transform: perspective(600px) rotateY(-20deg);
    animation: fireEffect 2s infinite;
  }
  
  .content {
    flex: 1;
    margin-left: 200px; /* Adjust to leave space for the left sidebar */
    margin-right: 200px; /* Adjust to leave space for the right sidebar */
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
  }
  
  @keyframes fireEffect {
    0% {
      box-shadow: 0 0 10px rgba(0, 0, 255, 0.3), 0 0 20px rgba(0, 0, 255, 0.5);
    }
    50% {
      box-shadow: 0 0 20px rgba(0, 0, 255, 0.6), 0 0 40px rgba(0, 0, 255, 1);
    }
    100% {
      box-shadow: 0 0 10px rgba(0, 0, 255, 0.3), 0 0 20px rgba(0, 0, 255, 0.5);
    }
  }
  
  .sidebar:hover {
    animation: blueFireHover 1s infinite;
  }
  
  @keyframes blueFireHover {
    0% {
      box-shadow: 0 0 15px rgba(0, 0, 255, 0.8), 0 0 30px rgba(0, 0, 255, 1);
    }
    100% {
      box-shadow: 0 0 25px rgba(0, 0, 255, 1), 0 0 50px rgba(0, 0, 255, 1);
    }
  }
  