.package-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  .package-card {
    background: #fff;
    border-radius: 15px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    padding: 20px;
    transition: transform 0.3s ease-in-out;
    transform-style: preserve-3d;
    perspective: 1000px;
    cursor: pointer;
  }
  
  .package-card h3 {
    font-size: 1.5rem;
    color: #333;
  }
  
  .package-card p {
    color: #666;
  }
  
  .package-card:hover {
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  }
  