
.teachers-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  max-width: 1200px;
  width: 100%;
  padding: 20px;
  box-sizing: border-box; /* Ensures padding is included in height */
  margin: 0 auto; /* Centers the container horizontally */
}

.teachers-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Ensures the container is centered vertically */
  padding: 20px;
  box-sizing: border-box;
}

/* Add this wrapper in the JSX around your teachers-container */

.teacher-card {
  background: #edf3ff;
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 20px;
  transition: transform 0.3s;
  box-sizing: border-box; /* Ensures padding is included in height */
}

.teacher-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 15px;
  border: 3px solid #487eea;
}

.teacher-card h2 {
  font-size: 1.2em;
  margin-bottom: 5px;
  color: #333;
}

.teacher-card p {
  margin: 5px 0;
  font-size: 0.9em;
  color: #777;
}

.teacher-link {
  display: inline-block;
  margin-top: 15px;
  padding: 10px 20px;
  background: #487eea;
  color: white;
  border-radius: 20px;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.3s;
}

.teacher-link:hover {
  background: #3666c5;
}

/* Responsive styles */
@media (max-width: 768px) {
  .teachers-container {
    grid-template-columns: repeat(2, 1fr);
  }

  .teacher-image {
    width: 150px;
    height: 90px;
  }

  .teacher-card h2 {
    font-size: 1em;
  }

  .teacher-card p {
    font-size: 0.8em;
  }

  .teacher-link {
    padding: 8px 16px;
  }
}

@media (max-width: 480px) {
  .teachers-container {
    grid-template-columns: 1fr;
  }

  .teacher-image {
    width: 120px;
    height: 80px;
  }

  .teacher-card h2 {
    font-size: 0.9em;
  }

  .teacher-card p {
    font-size: 0.7em;
  }

  .teacher-link {
    padding: 6px 12px;
  }
}
